:root {
  --font-body: "Clear Sans", arial;
}

:root {
  --text: hsl(0, 0%, 100%);
  --bg: hsl(248, 19%, 8%);
  --absent: hsl(0, 0%, 30%);
  --correct: hsl(115, 29%, 43%);
  --present: hsl(49, 51%, 47%);
  --key: hsl(200, 1%, 51%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: -webkit-fill-available;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  position: relative;
  color: var(--text);
  background-color: var(--bg);
  font-family: var(--font-body);
  text-transform: uppercase;
  min-height: 100vh;
  min-height: 100dvh;
  min-height: -webkit-fill-available;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

h1 {
  font-size: 2rem;
  font-size: clamp(1.5rem, 1.5rem + 2vw, 3.2rem);
  line-height: 1;
  padding-block: 0.625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  width: min(68.75rem, 100%);
}

.header {
  text-align: center;
  margin-bottom: 4em;
  border-bottom: 0.0625rem solid var(--absent);
}

.guess {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.4375rem;
  max-width: fit-content;
  margin: 0 auto 0.4375rem auto;
}
.guess > div[data-animation=flip] {
  animation-name: flip;
  animation-duration: 500ms;
  animation-timing-function: ease-in-out;
}
.guess__tile {
  display: flex;
  border: 0.125rem solid var(--absent);
  justify-content: center;
  align-items: center;
  font-size: clamp(1.5rem, 1.25rem + 1.1vw, 3.5rem);
  font-weight: 700;
  line-height: 1;
  width: 1.5em;
  height: 1.5em;
}
@keyframes pop {
  to {
    transform: scale(1.1);
  }
}
@keyframes flip {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(-90deg);
  }
  100% {
    transform: rotateX(0deg);
  }
}
.guess__tile[data-state=correct] {
  border-color: var(--correct);
  background-color: var(--correct);
}
.guess__tile[data-state=present] {
  border-color: var(--present);
  background-color: var(--present);
}
.guess__tile[data-state=absent] {
  border-color: var(--absent);
  background-color: var(--absent);
}
.guess__tile[data-animation=pop] {
  animation-name: pop;
  animation-duration: 100ms;
  animation-timing-function: ease-in;
  border-color: var(--key);
}

.keyboard {
  display: grid;
  justify-items: center;
  grid-template-columns: 1fr;
  row-gap: clamp(0.0625rem, 0.3vw, 0.3125rem);
  max-width: 37.5rem;
  height: fit-content;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 40em) {
  .keyboard {
    bottom: 0.625rem;
  }
}
.keyboard__row {
  display: flex;
  gap: clamp(0.0625rem, 0.3vw, 0.3125rem);
  touch-action: manipulation;
  width: 100%;
}
.keyboard__key {
  font-size: clamp(1rem, 0.5rem + 1vw, 1.5rem);
  font-family: inherit;
  font-weight: bold;
  border: 0;
  padding: 0.6em 0.5em;
  border-radius: 0.25rem;
  cursor: pointer;
  user-select: none;
  background-color: var(--key);
  color: var(--text);
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}
.keyboard__key[data-state=correct] {
  background-color: var(--correct);
}
.keyboard__key[data-state=present] {
  background-color: var(--present);
}
.keyboard__key[data-state=absent] {
  background-color: var(--absent);
}